<template>
    <va-modal  v-model="show" :hide-default-actions="true" :mobile-fullscreen="false" max-width="80vw">
        <div class="modal">
            <!-- TAREFAS -->
            <div class="modal-titulo">
                <h2 @click="dropdown1=!dropdown1">Tarefas</h2>
                <va-button color="verde" icon="add" @click="create_update('servico_subtask')" text-color="cinza" size="small">Adicionar</va-button>   
            </div>
            <span v-if="dropdown1" >
             <div style="background-color: var(--c1); padding: 10px;" v-for="item in objeto['subtasks']" :key="item">
                <div style="display:flex; flex-direction: row; justify-content: space-between; ">
                <va-checkbox :label="item.description" v-model="item.finish" color="verde" @click="atualizarTask(item)"/>
                <va-button text-color="verde" flat icon="edit" size="small" @click="create_update(item)" ></va-button>
                </div>
            </div>
            </span>
            <!-- COMENTRIOS -->
            <div class="modal-titulo">
                <h2 @click="dropdown2=!dropdown2">Comentrios</h2>
                <va-button color="verde" icon="add" @click="create_update('servico_comentario')" text-color="cinza" size="small">Adicionar</va-button>
            </div>
            <span v-if="dropdown2" >
            <div v-for="item in objeto['notes']" :key="item" class="modal-card">
                <div>
                    <div style="text-transform: uppercase; color: var(--azul)">{{item.description}}</div>
                    <div class="detalhes">
                        <va-icon name="person" size="small"/> {{getMembro(item.created_by).name}}
                        <va-icon name="calendar_month" size="small"/>  {{timestamp(item.created_at)}} 
                    </div>
                </div>
                <va-button text-color="verde" flat icon="edit" size="small" @click="create_update(item)" v-if="getUsuario.id == item.created_by "></va-button>
            </div>
            </span>
            <!-- CUSTOS -->
            <div class="modal-titulo">
                <h2 @click="dropdown3=!dropdown3">Custos</h2>
                <va-button color="verde" icon="add" @click="create_update('servico_custo')" text-color="cinza" size="small">Adicionar</va-button>
            </div>
            <span v-if="dropdown3" >
            <div v-for="item in objeto['custos']" :key="item" class="modal-card" >
                <div>
                    <div>{{item.description}}   ({{item.type}})</div>
                    <div class="detalhes">
                        <va-icon name="calendar_month" size="small"/> {{timestamp(item.created_at)}}
                    </div>
                </div>
                <div class="detalhes" >R$ {{item.value}} 
                    <va-button text-color="verde" flat icon="edit" size="small" @click="create_update(item)" ></va-button>
                </div>
            </div>
            <div class="modal-card" style="text-transform: uppercase; color: var(--azul); font-size: 1.2em; padding-right: 35px;"> 
                <div>TOTAL</div> 
                R${{getCustos(objeto['custos'])}} 
            </div>
            </span>
             <!-- MO DE OBRA -->
            <div class="modal-titulo">
                <h2 @click="dropdown4=!dropdown4">Mo de obra</h2>
                <va-button color="verde" icon="add" @click="create_update('servico_maodeobra')" text-color="cinza" size="small">Adicionar</va-button>
            </div>
            <span v-if="dropdown4" >
            <div v-for="item in objeto['mao_obra']" :key="item" class="modal-card" >
                <div>
                    <div>{{getMantenedor(item.responsible_id).name}}</div>
                    <div class="detalhes" >HH: {{duracao(item.dt_start,item.dt_end)}} min <va-icon name="calendar_month" size="small"/> {{item.dt_start}} / {{item.dt_end}}</div>
                </div>
                <!--<div style="text-align: right;">
                    <div style="color: var(--c3);"> <va-icon name="calendar_month" size="small"/> {{item.dt_start}}</div>
					<div style="color: var(--c3);">{{item.dt_end}}</div>
                </div>-->
                <va-button text-color="verde" flat icon="edit" size="small" @click="create_update(item)" ></va-button>
            </div>
            </span>
        </div>
    </va-modal>
</template>

<script>
export default {
    name: "ModalServicoConfig",
    props: ["aba"],
    data() {
        return {
            objeto: {},
            dropdown1: false,
            dropdown2: false,
            dropdown3: false,
            dropdown4: false,
        }
    },
    computed: {
        show: {
            get(){
                return this.$store.getters["servicos.getConfigShow"];
            },
            set(value) {
                this.$store.dispatch("servicos.showConfig_change", value);
            },
        },
        getConfigObjeto(){
            return this.$store.getters["servicos.getConfigObjeto"];
        },
        getUsuario(){
            return this.$store.getters["usuario.get"];
        }
    },
    watch: {
        getConfigObjeto(value){
            this.dropdown1 = false
            this.dropdown2 = false
            this.dropdown3 = false
            this.dropdown4 = false
            this.objeto = value
        }
    },
    methods: {
        create_update(objeto){
			this.$store.dispatch('edit-modal.editShow', {
				objeto: objeto,
                action: {
                    dispatch: 'servicos.showConfig', 
                    getters:  'servicos.get',
                    objeto: this.objeto
                },
                callback: this.aba.callback,
                id_parente: this.objeto.id
			})
		},
        async atualizarTask(task){
            this.$store.dispatch('loading.show')
            await task.update({ getters: this.$store.getters })
            this.$store.dispatch('toast.success', 'Atualizado');
			this.$store.dispatch('loading.hide')
        },
        getMembro(id){
			let membro =  this.$store.getters['membros.get'].filter(e => e.id == id);
			return membro[0]
		},
        getMantenedor(id){
			let membro =  this.$store.getters['mantenedores.get'].filter(e => e.id == id);
			return membro[0]
		},
		getCustos(custos){
			let total = 0
			for(let i = 0; i < custos.length; i++){
				total = total + custos[i].value
			}
			return total.toFixed(2)
		},
		timestamp(time_str){
			var date = new Date(time_str)
			return date.toLocaleDateString('pt-BR')
		},
		duracao(t0,t1){
			let duracao = new Date(t1) - new Date(t0)
			return duracao/(1000*60)
		},
    }
};
</script>

<style scoped>
.modal{
	min-width: 50vw ;
    color: var(--branco);
}
.modal-titulo{
    display: flex;
	justify-content: space-between;
    color: var(--branco);
    font-size: 1.3em; 
    padding: 5px 10px;
    margin: 10px 0 0 0;
	background-color: var(--c4);
}

.modal-card{
	padding: 10px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	/*margin-bottom: 5px;*/
    background-color: var(--c1);
}

.custos{
	padding: 10px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	color: var(--azul);
    font-size: 1.2em;
}
.detalhes{
    color: var(--c3)
}
h2 {
    width: 90%;
}
h2:hover{
    cursor: pointer;
}
</style>
